
body { font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif; margin: 0; }
.container { max-width: 900px; margin: 0 auto; padding: 16px; }
.nav { display:flex; gap:12px; padding:12px 0; border-bottom:1px solid #eee; margin-bottom:16px; }
.nav a { cursor:pointer; text-decoration:none; padding:6px 10px; border-radius:6px; color:#333; }
.nav a.active { background:#111; color:#fff; }
table { border-collapse: collapse; }
th, td { border:1px solid #ccc; padding:6px; font-size:14px; }
th { background:#f4f4f4; }
.hour-row { border-bottom:1px solid #eee; padding:4px 0; }
.event-row { display:flex; align-items:center; padding:2px 0; cursor:pointer; }
.event-row span.note { flex:1; font-size:12px; }
.editor { position: fixed; top: 64px; left: 50%; transform: translateX(-50%);
  background: #fff; box-shadow: 0 2px 12px rgba(0,0,0,0.2); padding: 16px; border-radius: 10px; z-index: 100; min-width: 360px; }
